*,
*::before,
*::after {
  box-sizing: border-box;
}

section {
  height: 100vh;
}

body {
  height: 100vh;
  font-family: "Montserrat", sans-serif;
  margin: 0;
}

img {
  max-width: 100%;
  display: block;
}

h1,
h2,
h3,
p {
  margin: 0;
}

.container {
  width: 100%;
}
